export default {
	props: {
		currentPage: {
			type: String,
			default: 'videoPage'
		}
	},
	data() {
		return {
			tabBar: [
				{
					url: 'videoPage',
					text: '视频',
					imgNormal: '/static/video_change.png',
					imgClick: '/static/video_index.png'
				},
				{
					url: 'musicPage',
					text: '音乐',
					imgNormal: '/static/music_change.png',
					imgClick: '/static/music_index.png'
				},
				{
					url: 'selfPage',
					text: '我的',
					imgNormal: '/static/my_change.png',
					imgClick: '/static/my_index.png'
				}
			]
		};
	},
	created() {
		uni.hideTabBar({})
	},

	computed: {},
	methods: {
		navTo(item) {
			if (item.url !== this.currentPage) {
				var isUrl = `/pages/${item.url}/${item.url}`;
				const that = this;
				uni.switchTab({
					url: isUrl
				});
			} else {
				this.$parent.toTop();
			}
		}
	}
};
